import React from "react";
import { Row, Col } from "reactstrap";
import { Link } from "react-router-dom";
import FeatherIcon from "feather-icons-react";

const modules = [
  {
    title: "Conceptos Claves",
    description: "Aprende los fundamentos esenciales de la huella de carbono, alcances, factores de emisión y acciones para reducir el impacto ambiental.",
    icon: "book-open",
    to: "/huella-carbono"
  },
  {
    title: "Calculadora",
    description: "Calcula la huella de carbono de tu organización de manera sencilla y visualiza resultados para tomar mejores decisiones.",
    icon: "activity",
    to: "/#calculadora"
  },
  {
    title: "Herramientas",
    description: "Accede a formatos, instructivos y recursos descargables para la gestión y reporte de sostenibilidad.",
    icon: "file-text",
    to: "/documentos"
  },
  {
    title: "Autodiagnóstico de sostenibilidad ambiental",
    description: "Descarga herramientas y realiza autodiagnósticos para impulsar la sostenibilidad en tu empresa.",
    icon: "check-circle",
    to: "/autogestion"
  }
];




const ModuleCards = () => (
  <section className="section" id="modules" style={{background: '#fff', padding: '2.5rem 0 2.5rem 0'}}>
    <div className="container">
      <Row className="justify-content-center mb-5">
        <Col lg={8} className="text-center">
          <h2 className="fw-bold text-mundo-verde">Explora los 4 módulos principales</h2>
          <p className="text-muted">
            Navega por los módulos clave de la plataforma Mundo Verde y accede a recursos, herramientas y conocimientos para impulsar la sostenibilidad en tu organización.
          </p>
        </Col>
      </Row>
      <Row className="justify-content-center">
        {modules.map((mod, idx) => (
          <Col key={idx} lg={3} md={6} sm={12} className="mb-4 d-flex align-items-stretch" style={{maxWidth: '370px', flex: '1 1 340px'}}>
            <Link to={mod.to} className="text-decoration-none w-100">
              <div
                className="service-box text-center px-5 py-2 position-relative card-mundo-verde module-card-hover"
                style={{
                  cursor: 'pointer',
                  minHeight: 80,
                  height: '100%',
                  maxWidth: 440,
                  minWidth: 280,
                  margin: '0 auto',
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  transition: 'transform 0.18s, box-shadow 0.18s, border 0.18s',
                }}
              >
                <div className="service-box-content p-2">
                  <div className="icon-mono service-icon avatar-md mx-auto mb-2">
                    <i>
                      <FeatherIcon icon={mod.icon} className="module-card-icon" size={38} />
                    </i>
                  </div>
                  <h4 className="mb-2 font-size-22 text-mundo-verde module-card-title">{mod.title}</h4>
                  <p className="text-muted mb-0 module-card-desc" style={{fontSize: '1.07rem'}}>{mod.description}</p>
                </div>
              </div>
            </Link>
          </Col>
        ))}
      </Row>
    </div>
  </section>
);

export default ModuleCards;
